/* Container */
.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  box-sizing: border-box;
}

/* Header */
.header {
  text-align: center;
  margin-bottom: 40px;
}

.title {
  font-size: 48px;
  color: #333;
  font-weight: bold;
  text-transform: uppercase;
}

/* Content */
.content {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Task Section */
.task-section {
  width: 100%;
  max-width: 800px;
  margin-top: 60px;
}

.section-title {
  font-size: 36px;
  margin-bottom: 20px;
  color: #333;
  font-weight: bold;
  text-transform: capitalize;
}

/* Tool */
.tool {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 40px;
}

.tool input[type="text"] {
  width: calc(100% - 160px);
  padding: 15px;
  margin-right: 20px;
  border: 2px solid #ccc;
  border-radius: 8px;
  font-size: 18px;
}

.tool button {
  background-color: #007bff;
  color: #fff;
  padding: 15px 30px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  font-size: 18px;
  margin-right: 20px;
  margin-bottom: 20px;
}

.tool button:hover {
  background-color: #0056b3;
}

/* Filter */
.filter {
  margin-top: 40px;
  text-align: center;
}

.filter span {
  font-size: 24px;
  margin-right: 20px;
}

/* Task List */
.task-list {
  list-style: none;
  padding: 0;
  width: 100%;
}

.task-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
  border-bottom: 2px solid #eee;
  cursor: pointer;
}

.task-item.completed {
  text-decoration: line-through;
  color: #999;
}

.task-item span {
  font-size: 18px;
}

.task-item button {
  background-color: #ff4444;
  color: #fff;
  padding: 12px 24px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.task-item button:hover {
  background-color: #cc0000;
}

/* Footer */
.footer {
  margin-top: 40px;
  text-align: center;
  color: #777;
}